var Keyhandler, _;

_ = require('underscore');

Keyhandler = (function() {
  function Keyhandler(handler, $1) {
    this.$ = $1;
    this._handler = _.compose(handler, this._extract_keycode).bind(this);
  }

  Keyhandler.from_handler = function(handler, $) {
    return new this(handler, $);
  };

  Keyhandler.prototype.reset = function() {
    this.$(document).off('keyup', this._handler);
    this.handle();
    return this;
  };

  Keyhandler.prototype._extract_keycode = function(ev) {
    return ev.which;
  };

  Keyhandler.prototype.handle = function() {
    this.$(document).on('keyup', this._handler);
    return this;
  };

  return Keyhandler;

})();

module.exports = {
  from_handler: Keyhandler.from_handler.bind(Keyhandler)
};
